@mixin flex($justify-content: center, $align-items: center) {
  display: flex;
  justify-content: $justify-content;
  align-items: $align-items;
}

.Header {
  @include flex;

  width: 100%;
  padding: 47px 0;
  align-items: center;
  position: relative;
}

.mobileHeader {
  position: relative;
  height: 60px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .mobileMenuIcon {
    height: 60%;
    filter: invert(88%) sepia(6%) saturate(904%) hue-rotate(314deg) brightness(95%) contrast(90%);
  }
  .mobileMenuLogo {
    height: 100%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

.Menu {
  width: 100%;
  @include flex;
  justify-content: space-between;
}

.MenuLogo {
  // margin: 0 76px;
  width: 200px;
  // height: 103.91px;
  img {
    width: 100%;
  }
}

.MenuItem {
  position: relative;
  // margin: 0 25px;
  cursor: pointer;
  text-decoration: none;
  img {
    width: 120px;
  }
  p {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -60%);
    font-weight: 700;
    font-size: 20px;
    line-height: 20px;
    color: #e6c5c5;
    margin: 0;
  }
  &.active,
  &:hover {
    p {
      color: #fecf76;
    }
  }
}

@media (max-width: 765px) {
  .Header {
    padding: 20px 0;
  }
  .Menu {
    position: absolute;
    transform: translateY(100%);
    bottom: 0;
    left: 0;
    z-index: 1;
    flex-direction: column;
    // align-items: flex-start;
    padding: 30px;
    row-gap: 10px;
    background-color: rgba($color: #000000, $alpha: 0.5);
    border-radius: 20px;
  }

  .MenuItem {
    position: relative;
    // margin: 0 25px;
    cursor: pointer;
    text-decoration: none;
    img {
      width: 120px;
    }
    p {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -60%);
      font-weight: 700;
      font-size: 20px;
      line-height: 20px;
      color: #e6c5c5;
      margin: 0;
    }
    &.active,
    &:hover {
      p {
        color: #fecf76;
      }
    }
  }
}
