@mixin flex($justify-content: center, $align-items: center) {
  display: flex;
  justify-content: $justify-content;
  align-items: $align-items;
}

.PreSale {
  position: relative;
  width: 100%;
}

.Banner {
  // width: 980px;
  // height: 280px;
  // left: 470px;
  // top: 279px;
  position: relative;
  width: 100%;

  background: #742611;
  border: 2px solid #ca5350;
  border-radius: 30px;
  overflow: hidden;

  .bg {
    width: 100%;
    // height: 100%;
  }
  .logo {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 30%;
    transform: translate(-50%, -50%);
  }
}

.Card {
  @include flex(space-between);
  gap: 20px;
  margin-top: 43px;
  align-items: stretch;
}

.CardItem {
  flex: 1;
  // width: 480px;
  // height: 439px;
  background: #742611;
  border-radius: 20px;
  padding: 20px;
}

.CardItemHeader {
  @include flex(flex-start);

  img {
    margin-right: 45px;
  }
}

.CardTitle {
  font-weight: 700;
  font-size: 32px;
  line-height: 36px;
  color: #e37a27;
}

.ClubCopies {
  @include flex(space-between);
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  color: #e37a27;
  margin-top: 38px;
}

.ClubCopiesTotal {
  color: #fff;
}

.ClubProgress {
  width: 420px;
  width: 100%;
  height: 18px;
  background: #491707;
  border: 1px solid #dc7526;
  border-radius: 9px;
  margin-top: 10px;
}

.ClubProgressValue {
  background: #e37a27;
  border-radius: 7px;
  width: 40%;
  height: 100%;
}

.ClubTotal {
  @include flex(space-between, flex-start);

  font-weight: 400;
  font-size: 20px;
  color: #e37a27;
  margin-top: 39px;
}

.ClubTotalValue {
  font-weight: 700;
  display: inline-block;
  text-align: right;
}

.ClubTotalBnb {
  font-weight: 700;
  font-size: 16px;
}

.Amount {
  @include flex(space-between, flex-start);
  background: #491707;
  border: 1px solid #dc7526;
  border-radius: 8px;
  width: 100%;
  height: 100px;
  margin-top: 30px;
  padding: 10px 20px;
  color: #e37a27;
  font-weight: 400;
  font-size: 20px;
  margin-bottom: 30px;

  input {
    background: none;
    outline: none;
    border: 0px;
    color: #fff;
  }
}

.AmountMax {
  font-weight: 700;
  font-size: 20px;
  color: #e37a27;
}

.Field {
  @include flex(space-between, flex-start);
  font-weight: 700;
  font-size: 20px;
  line-height: 36px;
  text-align: right;
  text-transform: uppercase;

  color: #e37a27;
}

.Button {
  margin-top: 30px;
}

.Tab {
  @include flex();
}

.TabItem {
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  text-align: center;
  color: #964817;
  margin: 48px;
  cursor: pointer;
}

.TabItemActive {
  color: #e37a27;
}

.TabTitle {
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;
  color: #e37a27;
  margin-bottom: 40px;
}

.TabContent {
  @include flex(space-between, flex-start);

  // height: 358px;
  height: fit-content;
  background: #491707;
  border: 1px solid #892721;
  border-radius: 30px;
}

.TabContentItem {
  flex: 1;
  width: 100%;
  max-width: 430px;
  padding: 40px 30px;
}

.TabField {
  @include flex(space-between);

  font-weight: 400;
  font-size: 20px;
  line-height: 36px;
  color: #964817;
}

.TabFieldValue {
  font-weight: 700;
  font-size: 20px;
  line-height: 36px;
  /* or 180% */

  text-align: right;
  text-transform: uppercase;

  color: #e37a27;
}

.TabContentItemAdd {
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  color: #e37a27;
  text-align: center;
  margin-top: 20px;
  cursor: pointer;
}

.TabContentItemButton {
  @include flex;

  width: 100%;
  height: 100%;
  padding-top: 30px;
}

@media (max-width: 765px) {
  .Card {
    align-items: center;
    flex-direction: column;
  }
  .CardItem {
    width: 100%;
  }

  .TabContent {
    flex-direction: column;
    align-items: center;
  }
  .TabContentItemButton {
    padding: 0;
  }
  .Button {
    display: flex;
    justify-content: center;
  }
}
