@mixin flex($justify-content: center, $align-items: center) {
  display: flex;
  justify-content: $justify-content;
  align-items: $align-items;
}

.IndexDesc {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  color: #e6c5c5;
  margin-top: 30px;

  p {
    margin-bottom: 32px;
  }
}

.IndexDescIcon {
  @include flex();
  width: 100%;
  align-items: stretch;
  gap: 15px;
  margin: 50px 0;
}

.IndexDescIconItem {
  @include flex();

  flex: 1;
  column-gap: 10px;
  align-items: center;
  // width: 300px;
  // height: 113px;
  background: #742611;
  border: 2px solid #ca5350;
  border-radius: 20px;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  color: #ffffff;
  padding: 30px 0;
  text-align: center;

  img {
    width: 80px;
    // margin-right: 33px;
  }
}

.Title {
  font-weight: 700;
  font-size: 48px;
  text-align: center;
  color: #ffffff;
}

.StatsIcon {
  @include flex(space-between, flex-start);
  width: 100%;
  flex-wrap: wrap;
  text-align: center;
  margin-top: 77px;
  row-gap: 30px;
}
.StatsIconItem {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.StatsIconContainer {
  @include flex;
  width: 70%;
  max-width: 150px;
  background: #742611;
  border: 2px solid #ca5350;
  border-radius: 50%;
}

.StatsIconItemText {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  /* identical to box height, or 120% */

  text-align: center;
  text-transform: uppercase;

  color: #ed6156;
}

.Activity {
  width: 100%;
  background: #742611;
  border: 2px solid #ca5350;
  border-radius: 30px;
  padding: 20px 40px 60px;
  margin-top: 49px;
  margin-bottom: 60px;
}

.ActivityTitle {
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  /* identical to box height, or 100% */

  text-align: center;
  text-transform: uppercase;

  color: #ffffff;
}

.ActivityList {
  @include flex(space-between);
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  /* identical to box height, or 120% */

  text-align: center;
  text-transform: uppercase;

  color: #ed6156;
  margin-top: 36px;
}

@media (max-width: 765px) {
  .IndexDescIconItem {
    font-size: 18px;
    line-height: 18px;
    padding: 15px 0;
    column-gap: 5px;
    img {
      width: 40px;
    }
  }
  .Title {
    font-size: 36px;
  }
  .StatsIconItem {
    width: 50%;
    flex: auto;
  }
}
